<template>
  <div>
    <input type="text" v-model="userId" />
    <br />
    <input type="text" v-model="userPw" v-on:keyup.enter="enter_Key" />
    <br />
    <input type="button" value="Login" v-on:click="insert_Value_Key(userPw)" />
    <p>관리자 ID : admin</p>
    <p>관리자 PW : 1212</p>
    <div v-if="ok">
      <h1>로그인 완료</h1>
    </div>
    <div v-else-if="ok2">
      <h1>관리자 아이디 혹은 비밀번호를 다시 확인하세요.</h1>
    </div>
    <div v-else-if="userId === '' || userPw === ''"></div>
    <div v-else-if="userId !== '' && userPw !== ''"></div>
  </div>
</template>
<script>
export default {
  components: {},
  data() {
    return {
      userId: '',
      userPw: '',
      ok: false,
      ok2: false
    }
  },
  setup() {},
  created() {},
  mounted() {},
  unmounted() {},
  methods: {
    insert_Value_Key: function (e) {
      var BreakException = {}
      try {
        if (this.userId === '' || this.userPw === '') {
          throw BreakException
        }
        if (this.userId === 'admin' && this.userPw === '1212') {
          this.ok = true
          this.ok2 = false
        } else {
          this.ok = false
          this.ok2 = true
        }
        this.userPw = ''
      } catch (e) {
        if (e !== BreakException) throw e
      }
    },

    enter_Key: function (e) {
      this.userPw = e.target.value
      this.insert_Value_Key(this.userPw)
    }
  }
}
</script>
